import React from 'react';
import './scss/myPageSignOut.scss';
import { Link, useLocation } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { signIn } from '../../reducer/signIn';
import { useNavigate } from 'react-router-dom';
import { messageModal } from '../../reducer/messageModal';
import { signUpModal } from '../../reducer/signUpModal';
import { signOutData } from '../../reducer/signOutData';
import axios from 'axios';

export default function MypageSignOutCom () {

    const location = useLocation();
    const navigate = useNavigate();
    const selector = useSelector((state)=>state);
    const dispatch = useDispatch();

    const okModalMethod=(msg)=>{
        const obj = {
            isOkModal: true,
            isOkMsg:msg,
        }
        dispatch(signUpModal(obj));
    }

    const [ state, setState ] = React.useState({
        radio: '',
        text: '',
        pw: '',
        id: '',
        name:'',
        hp:'',
        email:'',
        birth:'',
        address:'',

        newPw1:'',
        newPw2:''
    });
        
    React.useEffect(()=>{
        if( selector.signIn.signInData!==null && state.id==='' ){
            setState({
                ...state,
                id:       selector.signIn.signInData.id,
                pw:       selector.signIn.signInData.pw,
                name:     selector.signIn.signInData.name,
                hp:       selector.signIn.signInData.hp,
                email:    selector.signIn.signInData.email,
                birth:    selector.signIn.signInData.birth,
                address:  selector.signIn.signInData.address,
            });
        }
    },[selector.signIn.signInData]);

    const onChangeRadio=(e)=>{
        setState({
            ...state,
            radio: e.target.value
        })
    }

    const onChangeText=(e)=>{
        setState({
            ...state,
            text: e.target.value
        })
    }
    const onSubmitForm=(e)=>{
        e.preventDefault();

        if(localStorage.getItem('INNISFREE_SIGNIN_DATA')!==null){
            
            dispatch(signUpModal(true));
            okModalMethod('   ?');
        }
        else if(localStorage.getItem('INNISFREE_SIGNIN_DATA')===null){
            dispatch(signUpModal(false));
            dispatch(messageModal(false));
        }
    }

    return (
        <div id='sign_out'>
            <div className="container">
                <div className="gap">
                    <div className="title">
                        <h1></h1>
                    </div>
                    <div className="service_box">
                        <div className="sub_title"></div>
                        <div className="content">
                            <form onSubmit={onSubmitForm}>
                                <div className="alert_box">
                                    <ul>
                                        <li><img src="./images/ico_nodate.png" alt="" /></li>
                                        <li><span>      ?</span></li>
                                        <li><p> ,      . <br />     .</p></li>
                                    </ul>
                                    <div className="info_box">
                                        <ul>
                                            <li className='info_li'>
                                                <h4>1.        . -     </h4>
                                                <span>-  ,   ,   </span>
                                                <span>- 1:1   </span>
                                            </li>
                                            <li className='info_li'>
                                                <h4>2.    ,     . </h4>
                                                <span>-         </span>
                                                <span>-       ,    1     .</span>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                                <div className="title">
                                    <h1>  </h1>
                                </div>
                                <div className="sub_title pw"></div>
                                <ul className='input_box'>
                                    <li>
                                        <h2></h2>
                                        <input 
                                            type="password" 
                                            name='userPw'
                                            id='userPw'
                                            value={state.pw}
                                            className='input_obj'
                                            disabled
                                        />
                                    </li>
                                    <li>
                                        <h2></h2>
                                        <ul className="radio_box">
                                            <li>
                                                <label htmlFor="radio1">
                                                    <input 
                                                        type="radio" 
                                                        name='radio1'
                                                        id='radio1'
                                                        value={''}
                                                        onChange={onChangeRadio}
                                                        checked={state.radio.includes('')}
                                                    />
                                                    <span></span>
                                                </label>
                                            </li>
                                            <li>
                                                <label htmlFor="radio3">
                                                    <input 
                                                        type="radio" 
                                                        name='radio2'
                                                        id='radio3'
                                                        value={'  '}
                                                        onChange={onChangeRadio}
                                                        checked={state.radio.includes('  ')}
                                                    />
                                                    <span>  </span>
                                                </label>
                                            </li>
                                            <li>
                                                <label htmlFor="radio3">
                                                    <input 
                                                        type="radio" 
                                                        name='radio3'
                                                        id='radio3'
                                                        value={'  '}
                                                        onChange={onChangeRadio}
                                                        checked={state.radio.includes('  ')}
                                                    />
                                                    <span>  </span>
                                                </label>
                                            </li>
                                            <li>
                                                <label htmlFor="radio4">
                                                    <input 
                                                        type="radio" 
                                                        name='radio4'
                                                        id='radio4'
                                                        value={'     '}
                                                        onChange={onChangeRadio}
                                                        checked={state.radio.includes('     ')}
                                                    />
                                                    <span>     </span>
                                                </label>
                                            </li>
                                            <li>
                                                <label htmlFor="radio5">
                                                    <input 
                                                        type="radio" 
                                                        name='radio5'
                                                        id='radio5'
                                                        value={'  '}
                                                        onChange={onChangeRadio}
                                                        checked={state.radio.includes('  ')}
                                                    />
                                                    <span>  </span>
                                                </label>
                                            </li>
                                            <li>
                                                <label htmlFor="radio6">
                                                    <input 
                                                        type="radio" 
                                                        name='radio6'
                                                        id='radio6'
                                                        value={''}
                                                        onChange={onChangeRadio}
                                                        checked={state.radio.includes('')}
                                                    />
                                                    <span></span>
                                                </label>
                                            </li>
                                            <li>
                                                <textarea name="textarea" id="textarea" cols="30" rows="10" value={state.text} onChange={onChangeText}></textarea>
                                            </li>
                                        </ul>
                                    </li>
                                </ul>
                                <div className="update_button_box">
                                    <button type='submit'></button>
                                    <Link to="/index"></Link>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};