import React from "react";
import './scss/header.scss';
import {Link, Outlet, useLocation} from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import { searchModal } from "../reducer/searchModal";
import { shoppingBasketModal } from "../reducer/shoppingBasketModal";
import { useNavigate } from "react-router-dom";
import { signIn } from "../reducer/signIn";
import {adminsignIn} from "../reducer/adminsignIn";
import { messageModal } from "../reducer/messageModal";
import { cartMethod } from "../reducer/cartReducer";

export default function HeaderCom () {

    const dispatch = useDispatch();
    const selector = useSelector((state)=>state);
    const navigate = useNavigate();

    const searchSlide = React.useRef();
    const gnbMenu = React.useRef();

    React.useEffect(()=>{
        searchSlide.current.style.height = `${50 * 3}px`;
    },[]);
    
    const [state,setState] = React.useState({
        isFixed: false,
        isDrop: false,
        isMypage: false
    });

    const [cnt, setCnt] = React.useState(0);
    const [toggle, setToggle] = React.useState(0);

    const searchUpSlide=()=>{
        searchSlide.current.style.transition = 'all 0.3s ease-in';
        searchSlide.current.style.marginTop = `${-50 * cnt}px`;
        if(cnt>2) {
            searchSlide.current.style.transition = 'none';
            searchSlide.current.style.marginTop = 0;
            setTimeout(()=>{
                setToggle(0);
                setCnt(0);
            },10);

        }
    }

    React.useEffect(()=>{
        if(toggle===0){
            searchUpSlide();
        }
        else {
            setToggle(0);
            searchUpSlide();
        }
    },[cnt]);

    React.useEffect(()=>{
        let setId = 0;
        setId = setInterval(()=>{
            setCnt(cnt => cnt+1);
        }, 3000);
        return () => clearInterval(setId);
    },[]);

    React.useEffect(()=>{

        let row3Top = gnbMenu.current.offsetTop+50;
        window.addEventListener('scroll', function(){
            if(window.scrollY>=row3Top){
                setState({
                    ...state,
                    isFixed: true
                });
            }
            else {
                setState({
                    ...state,
                    isFixed: false
                });
            }
        });
    },[]);

    const onMouseEnterNav=()=>{
        setState({
            ...state,
            isDrop: true
        })
    }

    const onMouseLeaveNav=()=>{
        setState({
            ...state,
            isDrop: false
        })
    }

    const onClickSearch=(e)=>{
        e.preventDefault();
        dispatch(searchModal(true));
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        })
        const htmlEl = document.getElementsByTagName('html')[0];
        htmlEl.classList.add('on');
    }

    const onClickShoppingBasket=(e)=>{
        e.preventDefault();
        dispatch(shoppingBasketModal(true));
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        })
        const htmlEl = document.getElementsByTagName('html')[0];
        htmlEl.classList.add('on');
    }

     //  
   const messageModalMethod=(msg)=>{
    const obj = {
        isMessageModal: true,
        isMsg: msg
    }
    dispatch(messageModal(obj));
}

    const onClickLogIn=(e)=>{
        e.preventDefault();
        if(selector.adminsignIn.adminsignInData!==null){
            localStorage.removeItem('INNISFREE_ADMINSIGNIN_DATA');
            dispatch(adminsignIn(null));
            messageModalMethod('  .');
            navigate('/signIn');
        }
        else{
            navigate('/signIn');
        }
    }
    const onClickMyPage=(e)=>{
        e.preventDefault();
        navigate('/mypage');
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        })
        const htmlEl = document.getElementsByTagName('html')[0];
        htmlEl.classList.add('on');
    }

    const onClickLogOut=(e)=>{
        e.preventDefault();
        dispatch(signIn(null));
        localStorage.removeItem('INNISFREE_SIGNIN_DATA');
        sessionStorage.removeItem('INNISFREE_ADDRESS_KEY');
        localStorage.removeItem('SET_DB_CART');
        localStorage.removeItem('INNISFREE_CART_PRODUCT');
        localStorage.removeItem('INNISFREE_SHOPPING_LOG_PRODUCT');
        localStorage.removeItem('INNISFREE_VIEWPRODUCT');
        dispatch(cartMethod([]));
        navigate('/index');
    }
     
    
    const onClickAdminLogIn=(e)=>{
        e.preventDefault();
        if(selector.signIn.signInData!==null){
            localStorage.removeItem('INNISFREE_SIGNIN_DATA');
            dispatch(signIn(null));
            messageModalMethod('  .');
            navigate('/adminsignIn');
        }
        else{
            navigate('/adminsignIn');
        }
    }
    
    const onClickAdminLogOut=(e)=>{
        e.preventDefault();
        dispatch(adminsignIn(null));
        localStorage.removeItem('INNISFREE_ADMINSIGNIN_DATA');
        messageModalMethod('  .');
        navigate('/index');
    }

    //      

    const onClickUserinfoModify=(e)=>{
        e.preventDefault();
        navigate('/Userinfo')
    }
    return (
        <>
            <div id="header">
                <div className="my-util">
                    <div className="logo"><Link to='/index'></Link></div>
                    <div className="header-search">
                        <form action="" name="topSearchFrm" id="topSearchFrm" method="GET">
                            <fieldset className="keyword-input" onClick={onClickSearch}>
                                <input type="text" className="input-txt" id="querytop" autoComplete="off" />
                                <button type="button" className="btn-search"></button>
                                <div className="keyword-rolling">
                                    <ul ref={searchSlide}>
                                        <li>
                                            <span>  , NEW  </span>
                                            <a href="!#"></a>
                                        </li>
                                        <li>
                                            <span> M!  50%</span>
                                            <a href="!#"></a>
                                        </li>
                                        <li>
                                            <span>  , NEW  </span>
                                            <a href="!#"></a>
                                        </li>
                                    </ul>
                                </div>
                            </fieldset>
                        </form>
                    </div>
                    <div className="list-my-util">
                            {
                                selector.signIn.signInData===null &&
                                <button type="button" className="btn-login" onClick={onClickLogIn}><span className="name"></span></button>
                            }   
                   
                            {
                                selector.signIn.signInData!==null&&
                                <button type="button" className="btn-logOut" onClick={onClickLogOut}><span className="name"></span></button>
                            }
                            {
                                localStorage.getItem('INNISFREE_SIGNIN_DATA')!==null && (
                                    <button type="button" className="btn-mypage" onClick={onClickMyPage}><span className="name" ></span></button>
                                )
                            }
                            <button type="button" className="btn-cart-count" onClick={onClickShoppingBasket}>
                                <span className="name"></span>
                                {
                                    selector.cartReducer.cart.length >= 0 &&
                                    (                                                
                                        <span className="count">{selector.cartReducer.cart.length}</span>
                                    )
                                }
                            </button>                        
                            {
                                selector.adminsignIn.adminsignInData===null &&(
                                <button type="button" className="btn-admin" onClick={onClickAdminLogIn}><span className="name"></span></button>
                                )
                            }
                            {
                                selector.adminsignIn.adminsignInData!==null &&(
                                <button type="button" className="btn-admin-on" onClick={onClickAdminLogOut}><span className="name">{selector.adminsignIn.adminsignInData.name}</span></button>
                                )
                            }
                            {
                                selector.adminsignIn.adminsignInData!==null &&(
                                <button type="button" className="btn-user-info" onClick={onClickUserinfoModify}><span className="name"> </span></button>
                                )
                            }
                    </div>
                </div>
                <div id="gnbMenu" className={`gnb-menu${state.isFixed?' fixed':''}`} ref={gnbMenu}>
                    <div className="inner">
                        <div className="logo"></div>
                        <nav className="nav-gnb">
                            <button type="button" className={`btn-category${state.isDrop?' on':''}`} onMouseEnter={onMouseEnterNav}></button>
                            <ul className="gnb-list">
                                <li>
                                    <Link to='/sub1'>
                                    
                                    <strong className="icon-mark"><span className="ir"></span></strong>
                                    </Link>
                                </li>
                                <li>
                                    <Link to='/sub2'></Link>
                                </li>
                                <li>
                                    <Link to='/sub3'></Link>
                                </li>
                                <li>
                                    <Link to='/sub4'></Link>
                                </li>
                                <li>
                                    <Link to='/sub5'></Link>
                                </li>
                                <li>
                                    <Link to='/sub6'>FOR ME</Link>
                                </li>
                                <li className="active">
                                <Link to='/sub7'>ABOUT</Link>
                                </li>
                            </ul>
                            <div className="list-my-util">
                                <button type="button" className="btn-search" onClick={onClickSearch}></button>
                                {
                                    selector.signIn.signInData===null &&
                                    <button type="button" className="btn-login" onClick={onClickLogIn}></button>
                                }                       
                                {
                                    selector.signIn.signInData!==null&&
                                    <button type="button" className="btn-logOut" onClick={onClickLogOut}></button>
                                }
                                {
                                    selector.signIn.signInData !== null &&
                                    (
                                        <button type="button" className="btn-mypage" onClick={onClickMyPage}></button>
                                    )    
                                }
                                <button type="button" className="btn-cart-count" onClick={onClickShoppingBasket}>
                                    {
                                        selector.cartReducer.cart.length >= 0 &&
                                        (                                                
                                            <span className="count">{selector.cartReducer.cart.length}</span>
                                        )
                                    }
                                </button>  
                            </div>
                        </nav>
                    </div>
                    <div className="nav-category">
                        {
                            state.isDrop && (
                                <nav className="drop-category" onMouseLeave={onMouseLeaveNav}>
                                    <div className="inner">
                                        <ul className="list">
                                            <li className="active">
                                                <a href="!#"></a>
                                                <ul id="smenuUA">
                                                    <li><a href="!#">//</a></li>
                                                    <li><a href="!#">/</a></li>
                                                    <li><a href="!#">//</a></li>
                                                    <li><a href="!#"></a></li>
                                                    <li><a href="!#"></a></li>
                                                    <li><a href="!#">/</a></li>
                                                    <li><a href="!#">/</a></li>
                                                    <li><a href="!#">/</a></li>
                                                    <li><a href="!#"> </a></li>
                                                    <li><a href="!#"></a></li>
                                                </ul>
                                            </li>
                                            <li>
                                                <a href="!#"></a>
                                                <ul id="smenuQW">
                                                    <li><a href="!#"></a></li>
                                                    <li><a href="!#"></a></li>
                                                    <li><a href="!#"></a></li>
                                                    <li><a href="!#"></a></li>
                                                </ul>
                                            </li>
                                            <li>
                                                <a href="!#"></a>
                                                <ul id="smenuUB">
                                                    <li><a href="!#"></a></li>
                                                    <li><a href="!#"></a></li>
                                                    <li><a href="!#"> </a></li>
                                                </ul>
                                            </li>
                                            <li>
                                                <a href="!#">//</a>
                                                <ul id="smenuQF">
                                                    <li><a href="!#"></a></li>
                                                    <li><a href="!#">/</a></li>
                                                    <li><a href="!#"> /</a></li>
                                                    <li><a href="!#"> /</a></li>
                                                    <li><a href="!#"> /</a></li>
                                                    <li><a href="!#">/</a></li>
                                                </ul>
                                            </li>
                                            <li>
                                                <a href="!#"> </a>
                                                <ul className="smenuUT">
                                                    <li><a href="!#"> </a></li>
                                                </ul>
                                            </li>
                                            <li>
                                                <a href="!#"></a>
                                                <ul id="smenuUK">
                                                    <li><a href="!#"></a></li>
                                                    <li><a href="!#"> </a></li>
                                                    <li><a href="!#">/ </a></li>
                                                    <li><a href="!#">/</a></li>
                                                    <li><a href="!#">//</a></li>
                                                    <li><a href="!#"> </a></li>
                                                    <li><a href="!#"> </a></li>
                                                </ul>
                                            </li>
                                            <li id="tp3Frist">
                                                <a href="!#"> </a>
                                                <ul>
                                                    <li id="gominList1"><a href="!#">//</a></li>
                                                    <li id="gominList1"><a href="!#">//</a></li>
                                                    <li id="gominList1"><a href="!#">/</a></li>
                                                    <li id="gominList1"><a href="!#">/</a></li>
                                                    <li id="gominList1"><a href="!#">/</a></li>
                                                    <li id="gominList1"><a href="!#">/</a></li>
                                                    <li id="gominList1"><a href="!#">/</a></li>
                                                    <li id="gominList1"><a href="!#">/</a></li>
                                                </ul>
                                            </li>
                                        </ul>
                                    </div>
                                </nav>
                            )
                        }
                    </div>
                </div>
            </div>
            <Outlet />
        </>
    )
}