import React from 'react';
import './scss/sub3.scss'
import Sub3ComChild1 from './Sub3ComChild1';
import Sub3ComChild2 from './Sub3ComChild2';
import Sub3ComChild3 from './Sub3ComChild3';
import {Link, Outlet, useLocation} from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';

export default function Sub3Com({path}){

    const selector = useSelector((state)=>state);
    const [state,setState]=React.useState({
        tab1:true,
        tab2:false,
        tab3:false,
        btn1:true,
        btn2:false,
        btn3:false,
        btn4:false,
        btn5:false,
        btn6:false,
        btn7:false
    })

    //   
    const onClickTab1=(e)=>{
        e.preventDefault();
        setState({
            ...state,
            tab1:true,
            tab2:false,
            tab3:false
        })
    }
    const onClickTab2=(e)=>{
        e.preventDefault();
        setState({
            ...state,
            tab1:false,
            tab2:true,
            tab3:false
        })
    }
    const onClickTab3=(e)=>{
        e.preventDefault();
        setState({
            ...state,
            tab1:false,
            tab2:false,
            tab3:true
        })
    }

    //  
    const onClickbtn1=(e)=>{
        e.preventDefault();
        setState({
            ...state,
            btn1:true,
            btn2:false,
            btn3:false,
            btn4:false,
            btn5:false,
            btn6:false,
            btn7:false,
        })
    }
    const onClickbtn2=(e)=>{
        e.preventDefault();
        setState({
            ...state,
            btn1:false,
            btn2:true,
            btn3:false,
            btn4:false,
            btn5:false,
            btn6:false,
            btn7:false
        })
    }
    const onClickbtn3=(e)=>{
        e.preventDefault();
        setState({
            ...state,
            btn1:false,
            btn3:true,
            btn2:false,
            btn4:false,
            btn5:false,
            btn6:false,
            btn7:false
        })
    }
    const onClickbtn4=(e)=>{
        e.preventDefault();
        setState({
            ...state,
            btn1:false,
            btn4:true,
            btn3:false,
            btn2:false,
            btn5:false,
            btn6:false,
            btn7:false
        })
    }
    const onClickbtn5=(e)=>{
        e.preventDefault();
        setState({
            ...state,
            btn1:false,
            btn5:true,
            btn3:false,
            btn4:false,
            btn2:false,
            btn6:false,
            btn7:false
        })
    }
    const onClickbtn6=(e)=>{
        e.preventDefault();
        setState({
            ...state,
            btn1:false,
            btn6:true,
            btn3:false,
            btn4:false,
            btn5:false,
            btn2:false,
            btn7:false
        })
    }
    const onClickbtn7=(e)=>{
        e.preventDefault();
        setState({
            ...state,
            btn1:false,
            btn7:true,
            btn3:false,
            btn4:false,
            btn5:false,
            btn6:false,
            btn2:false
        })
    }

    return (
        <div id='sub3'>
            <div className="location">
                <div className="ineer-menu">
                    <div className="home"><a href="/index"></a></div>
                    <div className="navi">
                        <img src="./images/sub/sub1/arr_location_depth.png" alt="" />
                        <button><img src="./images/sub/sub1/arr_location_black.png" alt="" /></button>
                        <ul>
                            <li><a href="!#"></a></li>
                            <li><Link to='/sub1'></Link></li>
                            <li><Link to='/sub2'></Link></li>
                            <li><Link to='/sub3'></Link></li>
                            <li><Link to='/sub5'></Link></li>
                            <li><Link to='/sub6'>FOR ME</Link></li>
                            <li><Link to='/sub7'>ABOUT</Link></li>
                            <li><Link to='/sub9'></Link></li>
                            {
                                selector.signIn.signInData !== null &&
                                (                                    
                                    <li><Link to='/mypage'></Link></li>
                                )
                            }
                        </ul>
                    </div>
                </div>
            </div>
            <div className="container">
                <div className="title">
                    <h2></h2>
                </div>
                <div className="top-menu">
                        <div className="tab-btn-box">
                            <button className={state.tab1===true?'on':''} onClick={onClickTab1}></button>
                            <button className={state.tab2===true?'on':''} onClick={onClickTab2}> </button>
                            <button className={state.tab3===true?'on':''} onClick={onClickTab3}> </button>
                        </div>
                </div>
                <div className="tab-bar">
                    <ul>
                        <li>
                            <button className={state.btn1===true?'on':''} onClick={onClickbtn1}></button>
                        </li>
                        <li>
                            <button className={state.btn2===true?'on':''} onClick={onClickbtn2}></button>
                        </li>
                        <li>
                            <button className={state.btn3===true?'on':''} onClick={onClickbtn3}></button>
                        </li>
                        <li>
                            <button className={state.btn4===true?'on':''} onClick={onClickbtn4}></button>
                        </li>
                        <li>
                            <button className={state.btn5===true?'on':''} onClick={onClickbtn5}>//</button>
                        </li>
                        <li>
                            <button className={state.btn6===true?'on':''} onClick={onClickbtn6}></button>
                        </li>
                        <li>
                            <button className={state.btn7===true?'on':''} onClick={onClickbtn7}></button>
                        </li>
                    </ul>
                    {state.tab1===true&&<span class="date">2023.11.23 14:50 </span>}
                    {state.tab2===true&&<span class="date">2023.11.17 ~ 2023.11.23 </span>}
                    {state.tab3===true&&<span class="date"> </span>}
                </div>
                {state.tab1&&<Sub3ComChild1 btn1={state.btn1} btn2={state.btn2} btn3={state.btn3} btn4={state.btn4}btn5={state.btn5}btn6={state.btn6}btn7={state.btn7} path={path}/>}
                {state.tab2&&<Sub3ComChild2 btn1={state.btn1} btn2={state.btn2} btn3={state.btn3} btn4={state.btn4}btn5={state.btn5}btn6={state.btn6}btn7={state.btn7} path={path}/>}
                {state.tab3&&<Sub3ComChild3 btn1={state.btn1} btn2={state.btn2} btn3={state.btn3} btn4={state.btn4}btn5={state.btn5}btn6={state.btn6}btn7={state.btn7} path={path}/>}
            </div>
           
        </div>

    );
};
