import React from 'react';
import './scss/SignInCom.scss';
import { useNavigate } from 'react-router-dom';

export default function NonmemberCom () {

    const navigate = useNavigate();

    const [state, setState] = React.useState({
        loginGuideText: ''
    });

    //  =     
    const onClickIndexBtn=(e)=>{
        e.preventDefault();
        navigate('/');
    }

    //  &    
    const onClickIdOrderBtn=(e)=>{
        e.preventDefault();
        navigate();
    }
    return (
        <section id='signin' className='nonMember'>
            <div className="container">     
                <div className="content">
                    <div className="title">
                        <h1> </h1>
                        <button onClick={onClickIndexBtn} className='index_btn nonMember'>
                            <img src="./images/search/icon_left_arrow.png" alt="" />
                        </button>
                    </div>
                    <form autoComplete='off'>
                        <ul className="form_box">
                            <li className="login_box">
                                <input 
                                    type="text" 
                                    name='orderNumber' 
                                    id='orderNumber' 
                                    placeholder=''
                                    // value={state.loginId}
                                    // onChange={onChangeId}
                                />
                                <button className='del_btn' type='button'><img src="./images/signIn/icon_smallClose.png" alt="" /></button>
                            </li>
                            <li className="login_box">
                                <input 
                                    type="text" 
                                    name='orderNumber' 
                                    id='orderPw' 
                                    placeholder=''
                                />
                                <button className='del_btn' type='button'><img src="./images/signIn/icon_smallClose.png" alt="" /></button>
                                <p className={`hide_text${state.loginGuideText!=='' ? ' on' : ''}`}>   .</p>
                            </li>
                            <li className="login_btn">
                                <button className='non_member'> </button>
                            </li>
                        </ul>
                    </form>
                    <ul className="search_box non_member">
                        <li><a href="/idSearch" onClick={onClickIdOrderBtn}> </a></li>
                        <li><i></i><a  href="/pwSearch" onClick={onClickIdOrderBtn}> </a></li>
                    </ul>
                    <button className="signUp_btn">
                        <a href="!#">
                            <span>  ?</span>
                            <strong></strong>
                        </a>
                    </button>
                </div>
            </div>
        </section>
    );
};
