import React from 'react';
import './scss/sub6.scss'
import Sub6ComChild from './Sub6ComChild';
import Sub6ShoppingLogCom from './Sub6ShoppingLogCom';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';

export default function Sub6Com() {

    const selector = useSelector((state)=>state);
    
    return (
        <main id='sub6' className='sub'>
             <div className="location">
                <div className="ineer-menu">
                    <div className="home"><a href="/index"></a></div>
                    <div className="navi">
                        <img src="./images/sub/sub1/arr_location_depth.png" alt="" />
                        <button>FOR ME<img src="./images/sub/sub1/arr_location_black.png" alt="" /></button>
                        <ul>
                            <li><a href="!#"></a></li>
                            <li><Link to='/sub1'></Link></li>
                            <li><Link to='/sub2'></Link></li>
                            <li><Link to='/sub3'></Link></li>
                            <li><Link to='/sub5'></Link></li>
                            <li><Link to='/sub6'>FOR ME</Link></li>
                            <li><Link to='/sub7'>ABOUT</Link></li>
                            <li><Link to='/sub9'></Link></li>
                            {
                                selector.signIn.signInData !== null &&
                                (                                    
                                    <li><Link to='/mypage'></Link></li>
                                )
                            }
                        </ul>
                    </div>
                </div>
            </div>
            <div className="title-container">
                <div className="title-box">
                    <h2>FOR ME</h2>
                </div>
            </div>
            <div className="content">
                <div className="left-box">
                    <div className="recommend-box">
                        <div className="re-title-box">
                            <h3></h3>
                            <a href="!#"> </a>
                        </div>
                        <div className="re-content-box">

                        </div>
                        <p>     .</p>
                        <div className="btn-box">
                            <button></button>
                            <button> /  / </button>
                        </div>
                    </div>
                    <div className="content-container">
                        <Sub6ComChild/>
                    </div>
                </div>
                <Sub6ShoppingLogCom/>
            </div>
        </main>
    );
};